.button {
  background-color: #fff;
  border: 0;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  padding: 7px 14px;
  font-size: 8px;
  display: flex;
}

.button__span--arrow {
  padding-right: 15px;
}

.button__red {
  color: #fff;
  background-color: #d41342;
}

.button__red svg {
  fill: #fff;
}

.button__white {
  color: #d41342;
  background-color: #fff;
}

.button__white svg {
  fill: #d41342;
}

.button__white, .button__red {
  line-height: 10px;
}

.button__arrow {
  width: 40px;
  height: 40px;
  background: #fff url("button-arrow.5a357a40.png") center no-repeat;
}

.button__menu {
  width: 190px;
  padding: 12px 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 21px;
}

.button__section {
  width: 176px;
  padding: 12px 0;
  font-weight: 700;
  line-height: 18px;
}

.button__section--medium {
  font-size: 14px;
  line-height: 18px;
}

.button__section--big {
  font-size: 16px;
  line-height: 21px;
}

@media (min-width: 1200px) {
  .button__red--hero, .button__white--hero {
    padding: 12px 20px;
    font-size: 14px;
    line-height: 18px;
  }

  .button__menu {
    width: 160px;
  }

  .button__section--big {
    width: 200px;
  }
}

body {
  color: #000;
  letter-spacing: .03em;
  background-color: #fff;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

address {
  font-style: normal;
}

a {
  text-decoration: none;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

